<template>
  <div>
    <!-- 상단 메뉴 -->
    <HeaderMenu />
    <section :style="{ 'padding-top': `${mainPaddingTop}px` }">
      <router-view />
    </section>
  </div>
</template>
<script>
import { defineComponent, defineAsyncComponent, computed } from 'vue';

import { useStore } from 'vuex';
import { useRoute } from 'vue-router';

export default defineComponent({
  components: {
    HeaderMenu: defineAsyncComponent(() => import('./components/layout/HeaderMenu.vue')),
  },
  setup() {
    const route = useRoute();
    const store = useStore();

    const GNB_HEIGHT = store.getters['gnb/GNB_HEIGHT'];
    const GNB_BG_COLOR = store.getters['gnb/GNB_BG_COLOR'];

    const mainPaddingTop = computed(() => {
      const isIndexRoute = route.name == 'MainView' && route.path == '/' ? 0 : GNB_HEIGHT;
      return isIndexRoute;
    });

    const mainBg = computed(() => {
      return route.name == 'MainView' && route.path == '/' ? GNB_BG_COLOR : 'rgba(255, 228, 132, 0.5)';
    });

    return { mainPaddingTop, mainBg };
  },
});
</script>

