import { createStore } from 'vuex';
import createPersistedState from 'vuex-persistedstate';

import gnb from '@/store/gnb.js';

export default createStore({
  state: {
    userInfo: {
      userId: '',
      token: '',
      loginStatus: false,
    },
    loading: false,
    projectInfo: {
      projectName: '',
      projectNamespace: '',
    },
    dataInfo: {
      dataType: '',
      dataRows: 1,
      array: true,
      list: [],
      dataName: '',
      dataPath: '',
    },
  },
  getters: {
    // userInfo
    getUserId(state) {
      return state.userInfo.userId;
    },
    getToken(state) {
      return state.userInfo.token;
    },
    getLoginStatus(state) {
      return state.userInfo.loginStatus;
    },
    getLoading(state) {
      return state.loading;
    },
    // projetInfo
    getProjectName(state) {
      return state.projectInfo.projectName;
    },
    getProjectNamespace(state) {
      return state.projectInfo.projectNamespace;
    },
    // dataInfo
    getDataName(state) {
      return state.dataInfo.dataName;
    },
    getDataPath(state) {
      return state.dataInfo.dataPath;
    },
    getDataRows(state) {
      return state.dataInfo.dataRows;
    },
    getDataType(state) {
      return state.dataInfo.dataType;
    },
    getArrayCheck(state) {
      return state.dataInfo.array;
    },
    getDataRowList(state) {
      return state.dataInfo.list;
    },
    getDataRowListLength(state) {
      return state.dataInfo.list.length;
    },
    getDataRowType: state => index => {
      return state.dataInfo.list[index - 1].item.itemName;
    },
    getDataRowKeyName: state => index => {
      return state.dataInfo.list[index - 1].item.itemKeyName;
    },
    getDataRowItemOption: state => index => {
      return state.dataInfo.list[index - 1].item.itemOption;
    },
  },
  mutations: {
    setUserInfo(state, value) {
      state.userInfo.userId = value.userId;
      state.userInfo.token = value.token;
      state.userInfo.loginStatus = true;
    },
    removeUserInfo(state) {
      state.userInfo.userId = '';
      state.userInfo.token = '';
      state.userInfo.loginStatus = false;
    },
    startLoading(state) {
      state.loading = true;
    },
    stopLoading(state) {
      state.loading = false;
    },
    // 수정된 프로젝트
    modifyProject(state, value) {
      state.projectInfo.projectNamespace = value.projectNamespace;
    },
    // dataInfo 변경
    setDataName(state, value) {
      state.dataInfo.dataName = value.dataName;
    },
    setDataPath(state, value) {
      state.dataInfo.dataPath = value.dataPath;
    },
    setDataRows(state, value) {
      state.dataInfo.dataRows = value.dataRows;
    },
    setDataType(state, value) {
      state.dataInfo.dataType = value.dataType;
    },
    setArrayCheck(state) {
      state.dataInfo.array = true;
    },
    // dataInfo.list에 있는 특정 dataRow의 dataRowType 변경
    setDataRowType(state, value) {
      state.dataInfo.list[value.index - 1].categoryId = value.categoryId;
      state.dataInfo.list[value.index - 1].categoryName = value.categoryName;
      state.dataInfo.list[value.index - 1].item.itemId = value.itemId;
      state.dataInfo.list[value.index - 1].item.itemName = value.itemName;
    },
    // dataInfo.list에 있는 특정 dataRow의 dataRowKeyName 변경
    setDataRowKeyName(state, value) {
      state.dataInfo.list[value.index - 1].item.itemKeyName = value.dataRowKeyName;
    },
    setDataRowItemOption(state, value) {
      state.dataInfo.list[value.index - 1].item.itemOption = value.dataRowItemOption;
    },
    addDataRow(state) {
      state.dataInfo.list.push({
        categoryId: '1',
        categoryName: 'Basic',
        item: {
          itemId: '1',
          itemName: 'Boolean',
          itemKeyName: '',
          itemOption: {
            type: 'yes/no',
          },
        },
      });
    },
    // 특정 Data Row 삭제
    deleteDataRow(state, value) {
      state.dataInfo.list.splice(value.index - 1, 1);
    },
    setDataRowList(state, value) {
      state.dataInfo.list = value.list;
    },
    initDataInfo(state) {
      (state.dataInfo.dataName = ''),
        (state.dataInfo.dataPath = ''),
        (state.dataInfo.dataType = ''),
        (state.dataInfo.dataRows = 1),
        (state.dataInfo.array = true),
        (state.dataInfo.list = [
          {
            categoryId: '1',
            categoryName: 'Basic',
            item: {
              itemId: '1',
              itemName: 'Boolean',
              itemKeyName: '',
              itemOption: {
                type: 'yes/no',
              },
            },
          },
        ]);
    },
  },
  plugins: [createPersistedState()],
  modules: {
    gnb,
  },
});
